.menu{
    display: flex;
    flex-direction: column;
    gap: 2.3rem;
    /*height: 94vh;*/
    background-color: #F8F8FD;

}

.logo{
    text-align: center;
    padding: 20px;
    color: #27374d;
    align-items: center;
    display: flex;
    gap: 20px;

}

.logo .logoIcon{
    font-size: 1.4rem;
}

.menuList{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.menuList .item{
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 1.1rem;
    text-decoration: none;
    font-weight: 600;
    padding: 10px;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
    color: #27374d;
}
.menuList .item:hover{
    background-color: #27374d;
    color: #dce8f4;

}